import * as React from "react"
import { Link } from "gatsby"
import { Awards } from "../utils"

const CompetitionCard = ({ competition }) => {
  return (
    <Link
      to={competition.link}
      className="flex gap-4 items-center bg-white border-2 rounded-lg drop-shadow-xl p-6 w-full hover:-translate-y-2 transition-transform"
    >
      <img
        src={
          Awards.hasOwnProperty(competition.stats.highest)
            ? Awards[competition.stats.highest]
            : Awards[0]
        }
        alt=""
        className="max-h-24"
      />
      <h4 className="text-lg font-semibold">
        {competition.shortname || competition.name}
      </h4>
    </Link>
  )
}

export default CompetitionCard
